button {
  margin: 0 5px;
  padding: 10px;
  background-color: #ccc;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

.banner {
  width: 55%;
  margin: 0 auto;

}

.projects {
  width: 100%;
  display: block;
}

.button-container {
  padding: 10px;
  justify-content: space-between;
  margin: auto;
  width: 90%;
  text-align: center;
}

.work {
  display: inline-block;
  padding: 20px 20px;
  border-radius: 35px;
  background-image: linear-gradient(to right, #29323c, #485563, #2b5876, #4e4376);
  box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
  border: none;
  color: white !important;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  transition: transform 0.2s ease-in-out;
  width: 48%;
  font-weight: 500;



}

.play {
  background: rgba(59, 173, 227, 1);
  background: linear-gradient(45deg, rgba(59, 173, 227, 1) 0%, rgba(87, 111, 230, 1) 25%, rgba(152, 68, 183, 1) 51%, rgba(255, 53, 127, 1) 100%);
  display: inline-block;
  padding: 20px 20px;
  font-weight: 500;
  border-radius: 35px;
  box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
  border: none;
  color: white !important;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  font-size: 22px;
  text-align: center;
  text-decoration: none;
  transition: transform 0.2s ease-in-out;
  width: 48%;


}


.work:hover,
.play:hover {
  transform: scale(1.1);

}

/* recent  */
.recent-container {

  padding: 10px 0px 30px 0px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;

}







/* footer */
.footer-con {
  text-align: center;

}

footer {
  border-top: 2px solid black;
  width: 80%;
  margin: auto;


}

.left-div,
.right-div {
  display: inline-flex;
  width: 78%;
  text-align: start;
  color: #2C5AB4;
  height: 100%;
  align-items: flex-start;
}

.right-div {
  width: 22%;
  text-align: end;
  height: 100%;


}



.follow {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.box {
  width: 100px;
  height: 100px;
  background-color: gray;
}





@media only screen and (max-width: 768px) {

  .left-div,
  .right-div {
    display: block;
    width: 100%;
    text-align: center;
    height: 100%;
  }


}

/* about*/
.about-container {
  display: flex;
}

.left-about {
  float: left;
  width: 40%;
  height: 100%;   
}
.basic{
  color: #5f5f5f;
  font-weight: 400;
}
.basic-info{
  color:#2C5AB4;
  font-weight: 600;
}

.right-about {
  float: left;
  width: 60%;
  height: 100%;


   
}



@media screen and (max-width: 768px) {
  body {
    overflow-y: none;
  }

  .about-container {
    display: flex;
    flex-direction: column;
  }

  .left-about {
    width: 100%;
    height: 100%;
    text-align: center;
  }
  

  .right-about {
     
    width: 100%;
    height: 100%;
    
  }
 
  


}


/*close button*/



.close-container {
  position: absolute;
  margin: auto;
  width: 15px;
  height: auto;
  cursor: pointer;
}

.leftright {
  height: 4px;
  width: 20px;
  position: absolute;

  background-color: #000000;
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all .3s ease-in;
}

.rightleft {
  height: 4px;
  width: 20px;
  position: absolute;
  background-color: #000000;
  border-radius: 2px;
  transform: rotate(-45deg);
  transition: all .3s ease-in;
}

label {
  color: white;
  font-family: Helvetica, Arial, sans-serif;
  font-size: .6em;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: all .3s ease-in;
  opacity: 0;
}

.close {
  margin: 60px 0 0 5px;
  position: absolute;
}

.close-container:hover .leftright {
  transform: rotate(-45deg);
  background-color: #F25C66;
}

.close-container:hover .rightleft {
  transform: rotate(45deg);
  background-color: #F25C66;
}

.close-container:hover label {
  opacity: 1;
}


.workhard {
  background-image: linear-gradient(to right, #12090d, #0a1d7c);
  color: rgba(0, 0, 0, 0);
  -webkit-background-clip: text;
