  font-family: 'Gloock', serif !important;

}

.playhard {
  background-image: linear-gradient(to right, #ee2281, #4db8ee);
  color: rgba(0, 0, 0, 0);
  -webkit-background-clip: text;
  background-clip: text;
  font-family: 'Rubik Iso', cursive !important;

}